.update {
  width: 100%;
}

.update-profile-headline {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.update-row {
  background-color: white;
}

.update-img {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.update-texts-pro {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.update-header {
  font-size: 2rem;
  text-align: center;
  text-transform: uppercase;
}

.update-dataform {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.update-dataform-email {
  padding: .8rem;
  margin: 1rem;
  border-radius: 2.5px;
  outline: none;
  border: 1px solid rgb(229, 224, 224);
}

.update-dataform-button {
  padding: .8rem;
  margin: 1rem;
  border-radius: 2.5px;
  width: 13rem;
  outline: none;
  border: none;
}

.dont-have-acc {
  padding: .5rem;
}

.update-show-pass {
  position: fixed;
  top: 51%;
  left: 70%;
}

.update-image-button::-webkit-file-upload-button {
  padding: .8rem;
  margin: 1rem;
  border-radius: 30px;
  outline: none;
  border: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
  background-color: #ffffff;
  opacity: .4;
  margin-top: 10px;
  width: 50%;
}

.update-avatar {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
}